:root {
  --blue: #00acdc;
}

html {
  font-family: Roboto, Arial, serif;
}

body {
  color: #000;
  margin: 0;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}

.logo__img {
  max-width: 100%;
  display: block;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  display: flex;
}

.logo {
  align-items: center;
  width: 40px;
  height: 40px;
  text-decoration: none;
  display: inline-flex;
}

.nav {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-align: center;
  text-transform: uppercase;
  color: #000;
  justify-content: center;
  align-items: center;
  height: 60px;
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: inline-flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--blue);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 4px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}
